import { Web3ProviderEngine } from '@0x/subproviders'
import WalletConnectSubprovider from "@walletconnect/web3-subprovider"

import Connector, { ConnectorArguments } from './connector'

export default class WalletConnectConnector extends Connector {
  constructor(kwargs) {
    const { bridgeURL, ...rest } = kwargs
    super(rest)
    this.bridgeURL = bridgeURL
  }

  async onActivation() {
    if (!this.engine) {
      const engine = new Web3ProviderEngine()
      this.engine = engine
      this.engine.addProvider(new WalletConnectSubprovider({ bridge: this.bridgeURL}))
    }

    this.engine.start()
  }

  async getProvider() {
    return this.engine
  }

  async getAccount() {
    // dirty
    return this.engine._providers[0]._address
  }

  onDeactivation() {
    if (this.engine) {
      this.engine.stop()
    }
  }
}
